.button {
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  border: none;
  margin: 0;
  outline: none;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;

  cursor: pointer;
  background-color: #2eb4ff;
  color: white;

  transition: background-color 0.2s ease, opacity 0.3s ease;

  &:hover {
    background-color: darken(#2eb4ff, 10%);
  }

  &:active {
    background-color: darken(#2eb4ff, 20%);
  }

  &:disabled {
    opacity: 0;
  }

  justify-content: center;
  align-items: center;

  @media (min-width: 400px) {
    width: 3rem;
    height: 3rem;
  }
}
